<template>
  <div class="grocery-list-app" :class="{ '_edit': isEditing }">
    <div class="container">
      <!-- <router-link to="/" class="back-btn">Back</router-link> -->
      <form
        @submit.prevent="onSubmit"
        class="main-input"
      >
        <input
          type="text"
          placeholder="Find or create product"
          :value="inputText"
          @input="onInput"
          @focus="isEditing = false"
        />
        <button
          type="submit"
          class="btn btn_sm"
        >
          Add
        </button>
      </form>

      <div class="prod-list-wrapper">
        <div class="loader" v-if="isLoading">Loading...</div>

        <ul class="prod-list">
          <li
            v-for="item of sortedList"
            :key='item.id'
            class="prod-item"
            @click="onChange($event, item.id)"
          >
            <div class="edit-btns-wrapp">
              <button
                class="delete-item"
                type="button"
                @click.stop="removeItem(item.id)"
              ></button>
            </div>

            <div
              class="cat-line"
              :class="{ '_checked': item.isChecked }"
              ref="catRef"
            ></div>

            {{ item.title }}
          </li>
        </ul>
      </div>

      <div class="bottom-fix">
        <button class="edit-button" type="button" @click="editList">
          {{ this.isEditing ? 'Cancel' : 'Edit grocery list' }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import gsap from 'gsap'
import { collection, onSnapshot, addDoc, deleteDoc, doc, updateDoc, getDoc } from '@firebase/firestore'
import { database } from "@/database/db.js"

const collectionRef = collection(database, 'grocery-list')

export default {
  data() {
    return {
      unsub: null,
      list: [],
      inputText: '',
      isEditing: false,
      isLoading: true,
    }
  },
  created() {
    this.unsub = onSnapshot(collectionRef, (snapshot) => {
      this.list = snapshot.docs.map((doc) => {
        return {
          ...doc.data(),
          id: doc.id
        }
      })

      this.isLoading = false
    })
  },
  beforeDestroy() {
    this.unsub()
  },
  computed: {
    sortedList() {
      let updateList = this.list.slice()
        .sort((a, b) => a.title > b.title ? 1: -1)
        .sort((a, b) => a.isChecked - b.isChecked)

      if (this.inputText !== '') {
        updateList = updateList.filter((item) => item.title.includes(this.inputText))
      }

      return updateList
    },
  },
  methods: {
    onInput(e) {
      this.inputText = e.target.value.trim().toLowerCase()
    },
    async onSubmit() {
      const isDoubleValue = this.sortedList.find((v) => v.title === this.inputText)
      if (!this.inputText || isDoubleValue) {
        return
      }

      await addDoc(collectionRef, {
        title: this.inputText,
        isChecked: false,
      })

      this.inputText = ''
    },
    async onChange(e, id) {
      try {
        const docRef = doc(database, 'grocery-list', id)
        const docSnap = await getDoc(docRef)
        const isChecked = docSnap.data().isChecked
        const $catLine = e.target.closest('li').querySelector('.cat-line')

        const catAnimated = (width) => {
          gsap.to($catLine, {
            width: width,
            duration: 0.4,
            onComplete: async () => {
              await updateDoc(docRef, { isChecked: !isChecked })
              $catLine.style.width = ''
              this.inputText = ''
            }
          })
        }

        this.isEditing = false
        isChecked ? catAnimated(0) : catAnimated('100%')
      }
      catch(msg) {
        console.log(msg);
      }
    },
    removeItem(id) {
      const docRef = doc(database, 'grocery-list', id)
      deleteDoc(docRef)
    },
    editList() {
      this.inputText = ''
      this.isEditing = !this.isEditing
    }
  }
}
</script>
